






























import formMixin from "@/mixins/form"
import { Component, Vue, Prop, Watch,Mixins } from "vue-property-decorator";
@Component({})
export default class EditForm extends Mixins(formMixin) {
  @Prop(Object)
  ruleForm!: any;

  rules = {
    done: [{ required: true, message: "请选择是否解决", trigger: "change" }],
  };
 
  async handleConfirm() {
    let res=await (this as any).$refs.ruleForm.validate
    if (res) {
      let { _id, done } = this.ruleForm;
      let data = { _id, done };
      let cb=()=>{
        this.$bus.$emit("cancel")
      }
      done&&this.saveData(this.$API.bugModel.EditBug,data,cb)
    }
  }
  
  beforeDestroy() {
    this.$bus.$off("cancel")
  }
  
}
